<template>
    <div>
        <div  style="margin-left:50px;">
            <h3>{{ page_title }}</h3>
            </div>
            <div style="margin-left:50px;">
            <el-button size="mini" type="primary" @click="$router.go(-1)">返回</el-button>
        </div>
        <el-form
        class="mt15"
        :status-icon="true"
        :model="form"
        :rules="rules"
        ref="form"
        label-width="120px"
        label-position="right"
      >
            <el-form-item label="当前余额">
                <el-col :span="8">
                    <el-input v-model="form.pre_money" disabled="disabled"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="冲值金额"  prop="money">
                <el-col :span="8">
                    <el-input  type="number" v-model="form.money"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="充值方式" prop="type">
            <el-col :span="24">
                <el-radio-group v-model="form.type">
                  <el-radio-button label="1" >微信
                  </el-radio-button>
                  <el-radio-button label="2" >支付宝
                  </el-radio-button>
                  <el-radio-button label="3" >招商银行
                  </el-radio-button>
                </el-radio-group>
            </el-col>
        </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')">冲值</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>


<script>
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
    data () {
        return {
            page_title : '充值钱包',
            form : {
               money:'',
               pre_money:'',
               type : '1'
            },
            rules : {
                money: [
                    { required: true, message: "请输入充值金额", trigger: "blur" },
                ],
                type: [
                    { required: true, message: "请输入选择充值类型", trigger: "blur" },
                ],
            },
        }
    },

    created () {
        this.$api.get('enterprise/get_wallet',{}, res=>{
            this.form.pre_money = res.data.data.can_money;
        });
    },

    methods : {
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                this.$api.post('enterprise/add_money', this.form, res=>{
                    if(res.data.bol)
                    {
                    this.$message.success(res.data.msg);
                    setTimeout(() => {
                        this.$router.push('/admin/enterprise/wallet')
                    }, 1500);
                    }else{
                    this.$message.error(res.data.msg);
                    }
                });
                } else {
                console.log("error submit!!");
                return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    }
}
</script>


<style lang="" scoped>

</style>


